import { Component, Input, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { Camera, CameraResultType, CameraSource } from "@capacitor/camera";
import { Capacitor } from "@capacitor/core";
import { NavController, ToastController } from "@ionic/angular";
import { TranslateService } from "@ngx-translate/core";
import { SecureStoragePlugin } from "capacitor-secure-storage-plugin";
import { ClaimFormService } from "src/services/claim-form/claim-form.service";
import { ClaimReviewFormService } from "src/services/claim-review-form/claim-review-form.service";
import { TranslateConfigService } from "src/services/translate-config/translate-config.service";
import { UrlService } from "src/services/url/url.service";
@Component({
	selector: "app-bottom-popout",
	templateUrl: "./bottom-popout.component.html",
	styleUrls: ["./bottom-popout.component.scss"],
})
export class BottomPopoutComponent implements OnInit {
	@Input() type: number;
	@Input() size: number;

	readonly screenWidth = window.innerWidth;
	readonly screenHeight = window.innerHeight;

	modal: any;
	modalSize: number;

	gotoHome() {
		this.modal.dismiss();
		this.router.navigateByUrl("/home", { replaceUrl: true });
	}

	closeModal() {
		this.modal.dismiss();
	}

	async presentToast(position: 'top' | 'middle' | 'bottom', msg, color: string = "dark-green") {
		const toast = await this.toastController.create({
			message: msg,
			duration: 2000,
			position: position,
			color: color
		});

		await toast.present();
	}

	uploadDoc() {
		this.urlService.setPreviousUrl(this.router.url);
		if (this.router.url.includes("claim-history-detail")) {
			this.navCtrl.navigateForward("/upload-doc/history", {
				replaceUrl: true,
				animated: true,
			});
			return;
		}
		this.navCtrl.navigateForward("/upload-doc", {
			replaceUrl: true,
			animated: true,
		});
	}

	selectImageWeb(): Promise<{ webPath: string, format: string }> {
		return new Promise((resolve, reject) => {
			const input = document.createElement('input');
			input.type = 'file';
			input.accept = 'image/*';

			input.onchange = (event: any) => {
				const file = event.target.files[0];
				if (!file) {
					reject(new Error('No file selected'));
					return;
				}

				const mime = file.type;
				const format = mime.split('/')[1] || 'unknown';

				const reader = new FileReader();
				reader.onload = () => {
					resolve({ webPath: reader.result as string, format: format });
				};
				reader.onerror = () => reject(reader.error);
				reader.readAsDataURL(file);
			};

			input.onerror = () => {
				reject(new Error('File input error'));
			};

			input.click();
		});
	}

	async selectImageFromLibrary() {
		try {
			var image: any;

			if (Capacitor.getPlatform() === 'web') {
				image = await this.selectImageWeb();
				console.log(JSON.stringify(image))
			} else {
				if (Capacitor.isNativePlatform)  {
					await Camera.requestPermissions({ permissions: ['photos'] });
				}
				image = await Camera.getPhoto({
					quality: 85,
					allowEditing: false,
					resultType: CameraResultType.Uri,
					source: CameraSource.Photos 
				});
			}

			const imageId = Date.now().toString(36) + Math.random().toString(36).slice(2, 8);

			this.urlService.setPreviousUrl(this.router.url);
			if (this.router.url.includes("history")) {
				const formData = this.claimReviewFormService.getCurrentValue();
				this.claimReviewFormService.updateFormData({
					documents: [
						...formData?.documents,
						{
							id: imageId,
							filename: Date.now().toString() + "_doc." + image.format,
							image: image.webPath
						}
					]
				})
				this.navCtrl.navigateForward(`/previewuploaded/history/${imageId}`, {
					replaceUrl: true,
					animated: true,
				});
				return;
			}

			const formData = this.claimFormService.getCurrentValue();
			this.claimFormService.updateFormData({
				documents: [
					...formData?.documents,
					{
						id: imageId,
						filename: Date.now().toString() + "_doc." + image.format,
						image: image.webPath
					}
				]
			})
			this.navCtrl.navigateForward(`/previewuploaded/${imageId}`, {
				replaceUrl: true,
				animated: true,
			});
			
		} catch (error) {
		  	console.error('Error selecting image from library:', error);
		}
	}

	constructor(
		private navCtrl: NavController,
		public translate: TranslateService,
		private router: Router,
		private toastController: ToastController,
		public translateConfigService: TranslateConfigService,
		private claimFormService: ClaimFormService,
		private claimReviewFormService: ClaimReviewFormService,
		public urlService: UrlService
	) {
		this.modalSize =this.screenHeight * this.size;
	}

	async ngOnInit() {}
}
