import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { environment } from 'src/environments/environment';
import { ClaimFormService } from 'src/services/claim-form/claim-form.service';
import { ClaimReviewFormService } from 'src/services/claim-review-form/claim-review-form.service';
import { TranslateConfigService } from 'src/services/translate-config/translate-config.service';
import { UrlService } from 'src/services/url/url.service';

@Component({
  selector: 'app-previewuploaded',
  templateUrl: './previewuploaded.page.html',
  styleUrls: ['./previewuploaded.page.scss'],
})
export class PreviewuploadedPage implements OnInit, OnDestroy {
  image: any;
  id: string;
  readonly assetUrl = environment.assetURL;
  data: { id: string; filename: string; image: string; };
  loaded: boolean;
  saveDoc: boolean = false;
  previousUrl: string[];
  prevUrl: string;
  
  goBack() {
    if (this.prevUrl != null) {
			this.navCtrl.navigateBack(this.prevUrl, {
				replaceUrl: true,
				animated: true,
			});
			this.urlService.removePreviousUrl();
			return;
		}

    this.navCtrl.navigateBack("/claim-form-doc", {
      replaceUrl: true,
      animated: true,
    });
  }

  saveDocument() {
    this.saveDoc = true;
  }

  /**
   * delete image based on the image data
   * @param imageToDelete 
   */
  async removeDocumentByImage() {
    if (this.prevUrl.includes("history")) {
      const currentDocuments = this.claimReviewFormService.getCurrentValue().documents;
      const updatedDocuments = currentDocuments.filter(doc => doc.id !== this.id);
      this.claimReviewFormService.updateFormData({
        documents: updatedDocuments
      });
      return;
    }
    const currentDocuments = this.claimFormService.getCurrentValue().documents;
    const updatedDocuments = currentDocuments.filter(doc => doc.id !== this.id);
    this.claimFormService.updateFormData({
      documents: updatedDocuments
    });
  }

  constructor(
    private navCtrl:NavController,
    public translate: TranslateService,
    public translateConfigService: TranslateConfigService,
    private claimFormService: ClaimFormService,
    private claimReviewFormService: ClaimReviewFormService,
    private route: ActivatedRoute,
    public urlService: UrlService,
    public router: Router
  ) { }

  ngOnInit() {
    this.urlService.previousUrl$.subscribe((previousUrl: string[]) => {
			this.previousUrl = previousUrl;
		});
		this.prevUrl = this.previousUrl[this.previousUrl.length - 1];
  }
  
  ionViewWillEnter() {
    this.saveDoc = false;
    this.id = this.route.snapshot.paramMap.get("id");
    this.loaded= false;

    if (this.prevUrl.includes("history")) {
      const currentDocuments = this.claimReviewFormService.getCurrentValue().documents;
      this.data = currentDocuments.find(doc => doc.id === this.id);
    } else {
      const currentDocuments = this.claimFormService.getCurrentValue().documents;
      this.data = currentDocuments.find(doc => doc.id === this.id);
    }

    this.loaded = true;
  }

  ngOnDestroy() {
    if (!this.saveDoc) this.removeDocumentByImage();
  }
}
