import { Injectable } from "@angular/core";
import { BehaviorSubject, Observable } from "rxjs";

@Injectable({
	providedIn: "root",
})
export class UrlService {
	private urlTree: string[] = [];
	private disableBtn: boolean = false;
	private previousUrl: BehaviorSubject<string[]> = new BehaviorSubject<string[]>(this.urlTree);
	public previousUrl$: Observable<string[]> = this.previousUrl.asObservable();
	private disableBackBtn: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(this.disableBtn);
	public disableBackBtn$: Observable<boolean> = this.disableBackBtn.asObservable();

	/**
	 * disable back button
	 */
	setDisableBackBtn(disable: boolean) {
		this.disableBtn = disable;
		this.disableBackBtn.next(this.disableBtn);
	}
	/**
	 * save previous page url
	 */
	setPreviousUrl(previousUrl: string) {
		this.urlTree.push(previousUrl);
		this.previousUrl.next(this.urlTree);
	}

	/**
	 * delete previous page url
	 */
	removePreviousUrl() {
		if (this.urlTree.length > 0) {
			this.urlTree.pop();
			this.previousUrl.next(this.urlTree);
		}
	}

	/**
	 * delete all previous page url
	 */
	clearAllUrl() {
		this.urlTree = [];
		this.previousUrl.next(this.urlTree);
	}
}
