import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

export interface ClaimFormData {
  insuredPerson: string;
  contactNumber: string;
  bankName: string;
  bankAccountNumber: string;
  holderName: string;
  claimType: string;
  dateOfEvent: string; // ISO string (e.g., '2023-10-05')
  causeOfEvent: string;
  documents: {
    id: string;
    filename: string;
    image: string;
  }[];
  provideDocumentLater: boolean;
}

const STORAGE_KEY = 'claimReviewFormData';

@Injectable({
  providedIn: 'root'
})
export class ClaimReviewFormService {
  private formDataSubject = new BehaviorSubject<ClaimFormData>(this.getInitialData());
  public formData$ = this.formDataSubject.asObservable();

  constructor() {
    // Optional: Listen for storage changes (if multiple tabs)
    window.addEventListener('storage', (event) => {
      if (event.key === STORAGE_KEY) {
        this.formDataSubject.next(this.getInitialData());
      }
    });
  }

  // Get current form data as observable
  getFormData(): Observable<ClaimFormData> {
    return this.formData$;
  }

  // Get current form data as plain object
  getCurrentValue(): ClaimFormData {
    return this.formDataSubject.value;
  }

  // Update specific fields (partial update)
  updateFormData(partialData: Partial<ClaimFormData>): void {
    const current = this.formDataSubject.value;
    const updated = { ...current, ...partialData };
    this.formDataSubject.next(updated);
    this.saveToStorage(updated);
  }

  // Replace entire form data
  setFormData(data: ClaimFormData): void {
    this.formDataSubject.next(data);
    this.saveToStorage(data);
  }

  // Reset to initial empty state
  resetForm(): void {
    const emptyData: ClaimFormData = {
      insuredPerson: '',
      contactNumber: '',
      bankName: '',
      bankAccountNumber: '',
      holderName: '',
      claimType: '',
      dateOfEvent: '',
      causeOfEvent: '',
      documents: [],
      provideDocumentLater: false
    };
    this.formDataSubject.next(emptyData);
    localStorage.removeItem(STORAGE_KEY);
  }

  // Private helpers
  private saveToStorage(data: ClaimFormData): void {
    try {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(data));
    } catch (e) {
      console.error('Failed to save form data to localStorage', e);
    }
  }

  private getInitialData(): ClaimFormData {
    try {
      const saved = localStorage.getItem(STORAGE_KEY);
      if (saved) {
        return JSON.parse(saved) as ClaimFormData;
      }
    } catch (e) {
      console.error('Failed to parse saved form data', e);
    }
    return this.getEmptyData();
  }

  private getEmptyData(): ClaimFormData {
    return {
      insuredPerson: '',
      contactNumber: '',
      bankName: '',
      bankAccountNumber: '',
      holderName: '',
      claimType: '',
      dateOfEvent: '',
      causeOfEvent: '',
      documents: [],
      provideDocumentLater: false
    };
  }
}
